@use '../base';
@use '../utils';

.header {
    position: relative;

    @include utils.mQ(38.75rem) {
        display: grid;
        grid-template-columns: 0 5fr 4fr;
    }

    @include utils.mQ(67.5rem) {
        grid-template-columns: 3fr 4fr 2fr;
    }
}

.hero {
    @include utils.mQ(38.75rem) {
       // justify-content: flex-start;
       // flex: 1 1 60%;
    }
}

.main-content {
    @include utils.mQ(38.75rem) {
        display: flex;
        flex-direction: column;
        align-items: flex-start;
        justify-content: center;
        margin-inline-start: -3.8rem;
    }
}

.decorative {
    background-color: base.$clr-dark-grey;
}

.main-heading {
    font-weight: base.$fw-black;
    max-width: 28.125rem;
    @include utils.mQ(67.5rem) {
        position: absolute;
        left: 13.5%;
        color: white;
        mix-blend-mode: difference;
    }
}

.main-content-text {
    font-size: base.$fs-18;
    font-weight: base.$fw-light;
    margin-block: base.$spacer-2;
}

.link-btn {
    font-size: base.$fs-20;
    background-color: base.$clr-orange;
    color: base.$clr-white;
    display: inline-flex;
    align-items: center;
    text-decoration: none;

    span {
        background-color: base.$clr-dark-grey;
        padding: base.$spacer-1;
    }

    img {
        padding: base.$spacer-1;
    }
}

.hero-img {
    width: 100%;
    object-fit: cover;
}